import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { withRouter } from '@Green-Dot-Corporation/eureka-lib-router-utils';
import {
    Heading,
    Hyperlink,
} from '@Green-Dot-Corporation/eureka-ui-typography';
import {
    SuspensionComposer,
    raisePageError,
} from '@Green-Dot-Corporation/eureka-ui-loading';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import {
    getTranslation,
    reactI18next,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
// Vendor SDK
import { Atomic, Product } from '@atomicfi/transact-javascript';
import DDSwitchContainer from '../dd-switch-container/DDSwitchContainer';
// Services
import {
    getAccountData,
    getTerms,
    requestDDSwitchVendorToken,
    updateSwitchAttempt,
} from '../../services/DDSwitchService';
import { ROUTES_CONSTS } from '../../const/routesConst';
import { SWITCH_EVENT_STATUS } from '../../const/switchEventConst';
import './styles/bump.scss';
import Img from '../img/Img';

const { PRIMARY } = VARIANTS;
const ATOMIC_TERM_TYPE = 22;
const BRAND_COLOR = '#2945ff';
const OVERLAY_COLOR = '#f7f7f7';

class Bump extends PureComponent {
    static propTypes = {
        navigateWithinParent: PropTypes.func,
        onSuspensionFinish: PropTypes.func,
        onSuspensionStart: PropTypes.func,
        container: PropTypes.string,
    };

    static defaultProps = {
        onSuspensionFinish: () => Promise.resolve(),
        onSuspensionStart: () => Promise.resolve(),
    };

    state = { atomicTermsUrl: '' };

    render() {
        return (
            <DDSwitchContainer contextCls={`${this.baseCls}__container`}>
                <Callout>
                    {this.renderImgs()}

                    <div className={`${this.baseCls}__text-container`}>
                        {this.renderTitle()}
                        {this.renderSubTitle()}
                        {this.renderBumpContent()}
                    </div>

                    <div className={`${this.baseCls}__actions-container`}>
                        {this.renderContinueBtn()}
                    </div>

                    {this.renderNote()}
                </Callout>
            </DDSwitchContainer>
        );
    }

    componentDidMount() {
        this.fetchAccountInfo();

        if (this.resetContainer) {
            this.resetContainer();
        }
    }

    renderImgs = () => {
        return (
            <div className={`${this.baseCls}__imgs-container`}>
                <span className={`${this.baseCls}__img-brand`}>
                    <Img
                        filename="mini-logo-brand.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.brandLogo')}
                    />
                </span>
                <span className={`${this.baseCls}__img-atomic`}>
                    <Img
                        filename="mini-logo-atomic.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.atomic')}
                    />
                </span>
            </div>
        );
    };

    renderTitle = () => {
        const headingLvl = 5;
        const headingText =
            getTranslation('brand.displayed.name') +
            getTranslation('ddswitch.bump.title');

        return (
            <Heading
                contextCls={`${this.baseCls}__title`}
                headingLvl={headingLvl}
            >
                {headingText}
            </Heading>
        );
    };

    renderSubTitle = () => {
        const text = getTranslation('ddswitch.bump.subtitle');

        return <div className={`${this.baseCls}__subtitle`}>{text}</div>;
    };

    renderBumpContent = () => {
        const { Trans } = reactI18next;

        return (
            <div className={`${this.baseCls}__bump-content`}>
                <p className={`${this.baseCls}__bump-content-1`}>
                    <Img
                        filename="number-1.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number1')}
                    />
                    <span>{getTranslation('ddswitch.bump.content.one')}</span>
                </p>
                <p className={`${this.baseCls}__bump-content-2`}>
                    <Img
                        filename="number-2.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number2')}
                    />
                    <span>{getTranslation('ddswitch.bump.content.two')}</span>
                </p>
                <p className={`${this.baseCls}__bump-content-3`}>
                    <Img
                        filename="number-3.svg"
                        alt={getTranslation('ddswitch.bump.imgAlt.number3')}
                    />
                    <span>
                        <Trans
                            i18nKey="ddswitch.bump.content.three"
                            values={{
                                brandDisplayedName: getTranslation(
                                    'brand.displayed.name',
                                ),
                            }}
                        />
                    </span>
                </p>
            </div>
        );
    };

    renderContinueBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__button-continue`)}
                text={getTranslation('ddswitch.bump.btn.text')}
                variant={PRIMARY}
                onClick={this.handleContinueBtnClick}
                aria-label={getTranslation('ddswitch.bump.btn.text')}
            />
        );
    };

    renderNote = () => {
        const { Trans } = reactI18next;
        const { atomicTermsUrl } = this.state;

        return (
            <div className={`${this.baseCls}__note`}>
                <Trans
                    i18nKey="ddswitch.bump.note"
                    values={{
                        brandDisplayedName: getTranslation(
                            'brand.displayed.name',
                        ),
                    }}
                    components={{
                        termsLink: (
                            <Hyperlink
                                shouldOpenInNewWindow
                                href={atomicTermsUrl}
                            />
                        ),
                        ppLink: (
                            <Hyperlink
                                shouldOpenInNewWindow
                                href={atomicTermsUrl}
                            />
                        ),
                    }}
                />
            </div>
        );
    };

    renderContainer = () => {
        const { container } = this.props;

        if (!container || !document.querySelector(container)) {
            return undefined;
        }
        this.atomicContainer?.remove();
        this.atomicContainer = document.createElement('div');
        this.atomicContainer.id = 'ddSwitchContainer';
        this.atomicContainer.className = 'dds-container';
        this.atomicContainer.innerHTML = this.renderContainerInnerHtml();
        document.body.appendChild(this.atomicContainer);
        document.querySelector(container).style.display = 'none';

        return () => {
            this.atomicContainer?.remove();
            document.querySelector(container).style.display = 'initial';
        };
    };

    renderContainerInnerHtml = () => {
        return `
            <div class="dds-container__overlay-wrap">
                <div class="dds-container__page-wrap">
                </div>
            </div>
        `;
    };

    handleContinueBtnClick = async () => {
        const { onSuspensionStart, onSuspensionFinish } = this.props;

        try {
            onSuspensionStart();
            const resp = await requestDDSwitchVendorToken();
            this.setUpAtomicTransact(resp);
        } catch (e) {
            onSuspensionFinish();
            const { errorCode } = e ?? {};

            // Access denied due to attempt limit
            if (errorCode === 1370072) {
                const path = ROUTES_CONSTS.BUMP_ATTEMPT_DECLINED;
                const { navigateWithinParent } = this.props;
                navigateWithinParent(path);

                return;
            }
            raisePageError();
        }
    };

    fetchAccountInfo = async () => {
        let accountData = {};

        try {
            accountData = await getAccountData();
            accountData.atomicTermsUrl = await this.fetchAtomicTermsUrl(
                accountData.accountidentifier,
            );
        } catch (error) {
            raisePageError();
        } finally {
            this.setState({
                ...accountData,
            });
        }
    };

    fetchAtomicTermsUrl = async (accountIdentifier) => {
        let atomicTermsUrl = '';

        const terms = await getTerms(accountIdentifier);

        const atomicTerms = terms.terms.filter(
            (i) => i.termtype === ATOMIC_TERM_TYPE,
        )[0];
        const { url } = atomicTerms;
        atomicTermsUrl = url;

        return atomicTermsUrl;
    };

    setUpAtomicTransact = (switchAttempt) => {
        const { onSuspensionFinish, container } = this.props;
        const { vendorToken, switchId } = switchAttempt;

        this.resetContainer = this.renderContainer();

        try {
            Atomic.transact({
                config: {
                    publicToken: vendorToken,
                    tasks: [
                        {
                            product: Product.DEPOSIT,
                        },
                    ],
                    theme: {
                        display: 'inline',
                        brandColor: BRAND_COLOR,
                        overlayColor: OVERLAY_COLOR,
                    },
                    deeplink: { step: 'search-company' },
                },
                container:
                    container && document.querySelector(container)
                        ? '.dds-container__page-wrap'
                        : '',
                onInteraction: (interaction) => {
                    switch (interaction.name) {
                        case 'Initialized Transact':
                            onSuspensionFinish();
                            break;
                        case 'Viewed High Latency Page':
                        case 'Viewed Task Completed Page': {
                            const state = SWITCH_EVENT_STATUS.SUBMITTED;

                            this.updateSwitchAttempt(switchId, state);
                            break;
                        }
                        default:
                            break;
                    }
                },
                onFinish: () => {
                    this.navigateToLandingPage();
                },
                onClose: (data) => {
                    if (data.reason === 'unknown' && !data.taskId) {
                        this.updateSwitchAttempt(
                            switchId,
                            SWITCH_EVENT_STATUS.ABORTED,
                        );
                    }

                    this.navigateToLandingPage();
                },
            });
        } catch (error) {
            // Currently, cannot handle the error 'Unhandled postMessage Event undefined' in SDK
            if (this.resetContainer) {
                this.resetContainer();
            }
        }
    };

    updateSwitchAttempt = async (switchId, status) => {
        let resp;

        try {
            resp = await updateSwitchAttempt(switchId, status);
        } catch (error) {
            // no need handle error in order not to block user
        }

        return resp;
    };

    navigateToLandingPage = () => {
        const path = ROUTES_CONSTS.LANDING;
        const { navigateWithinParent } = this.props;
        navigateWithinParent(path);

        if (this.resetContainer) {
            this.resetContainer();
        }
    };

    baseCls = 'dd-switch-bump';
}

export default withRouter(
    SuspensionComposer(Bump, () => ({
        title: getTranslation('statusModal.loading'),
    })),
);
