import { getPlatform } from './urlHelper';
import { PREFIX_URL_CONFIG } from '../const/urlConst';

export default {};

let applicationId;
let prefixAPIUrl;
let env;

export const getApplicationId = () => applicationId;

export const setApplicationId = (value) => {
    applicationId = value;
};

export const getEnv = () => env;

export const setEnv = (value) => {
    env = value;
};

export const getPrefixAPIUrl = () => prefixAPIUrl;

export const setPrefixAPIUrl = () => {
    const defaultUrl = PREFIX_URL_CONFIG[getPlatform()][getEnv()];

    prefixAPIUrl = defaultUrl;
};

export const cleanData = () => {
    applicationId = '';
    prefixAPIUrl = '';
    env = '';
};
