import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {
    ARIAAccordion,
    AccordionItemTrigger,
    AccordionContentComposer,
} from '@Green-Dot-Corporation/eureka-ui-accordions';
import { withRouter } from '@Green-Dot-Corporation/eureka-lib-router-utils';
import { Banner, Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import {
    Button,
    LinkButton,
    VARIANTS,
} from '@Green-Dot-Corporation/eureka-ui-buttons';
import {
    Heading,
    Hyperlink,
} from '@Green-Dot-Corporation/eureka-ui-typography';
import {
    SuspensionComposer,
    raisePageError,
} from '@Green-Dot-Corporation/eureka-ui-loading';
import {
    EurekaMoneySymbolFast,
    EurekaMoneySymbolZero,
    EurekaShieldMoney,
    EurekaCircleInfo,
} from '@Green-Dot-Corporation/eureka-ui-icons';
import {
    List,
    ListItem,
    ResourceListItem,
} from '@Green-Dot-Corporation/eureka-ui-lists';
import {
    Toaster,
    TOASTER_VARIANTS,
} from '@Green-Dot-Corporation/eureka-ui-toasters';
import {
    getTranslation,
    reactI18next,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import { saveAs } from 'file-saver';
import DDSwitchContainer from '../dd-switch-container/DDSwitchContainer';
import { ROUTES_CONSTS } from '../../const/routesConst';
import Legal from '../legal/Legal';
// Services
import {
    getAccountData,
    getHelpCenterInfo,
    getDDManualForm,
} from '../../services/DDSwitchService';
import Header from '../header/Header';
import './styles/landing.scss';
import Img from '../img/Img';

const { PRIMARY, SECONDARY } = VARIANTS;

class Landing extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        brand: PropTypes.string,
        isAutoSwitchEnabled: PropTypes.bool,
        navigateWithinParent: PropTypes.func,
        onSuspensionFinish: PropTypes.func,
        onSuspensionStart: PropTypes.func,
    };

    static defaultProps = {
        onSuspensionFinish: () => Promise.resolve(),
        onSuspensionStart: () => Promise.resolve(),
    };

    state = {
        shouldShowLandingPage: false,
        faqLink: '',
    };

    render() {
        const { shouldShowLandingPage } = this.state;
        const { contextCls } = this.props;

        return (
            <div className={cx(this.baseCls, contextCls)}>
                {shouldShowLandingPage && this.renderLandingPage()}
            </div>
        );
    }

    componentDidMount() {
        this.fetchAccountData();
        this.fetchFaqLink();
    }

    renderLandingPage = () => {
        const { contextCls, ...rest } = this.props;

        return (
            <DDSwitchContainer
                {...rest}
                contextCls={`${this.baseCls}__landing`}
            >
                {this.renderHeader()}
                {this.renderContent()}
            </DDSwitchContainer>
        );
    };

    renderHeader = () => {
        return (
            <Header
                headerTitle={getTranslation('landing.header')}
                headingLvl={1}
            />
        );
    };

    renderContent = () => {
        return (
            <div className={`${this.baseCls}__landing-content`}>
                <Callout>
                    <div className={`${this.baseCls}__img-container`}>
                        {this.renderDDImg()}
                    </div>

                    <div className={`${this.baseCls}__text-container`}>
                        {this.renderTitle()}
                        {this.renderExclusivePerksForLargeScreen()}
                        {this.renderExclusivePerksForSmallScreen()}
                        {this.renderInfoBanner()}
                        {this.renderAccountInfo()}
                    </div>

                    <div className={`${this.baseCls}__actions-container`}>
                        {this.renderActionButtons()}
                    </div>
                </Callout>

                {this.renderTips()}
                <Legal />
            </div>
        );
    };

    renderDDImg = () => {
        return (
            <Img
                filename="direct-deposit.svg"
                alt={getTranslation('landing.imgAlt.directDeposit')}
            />
        );
    };

    renderTitle = () => {
        const headingLvl = 2;
        const headingText = getTranslation('landing.heading.text');

        return (
            <Heading
                contextCls={`${this.baseCls}__title`}
                headingLvl={headingLvl}
            >
                {headingText}
            </Heading>
        );
    };

    renderExclusivePerksForLargeScreen = () => {
        const { Trans } = reactI18next;
        const perkEarlyTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.early.text.msg"
                components={{ sup: <sup /> }}
            />
        );
        const perkNoFeeTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.noFee.text.msg"
                components={{ sup: <sup /> }}
            />
        );
        const perkDDProtectionTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.odProtection.text.msg"
                components={{ sup: <sup /> }}
            />
        );

        return (
            <div
                className={`${this.baseCls}__exclusive-perks-container--large-screen`}
            >
                <Callout
                    contextCls={`${this.baseCls}__callout-icon`}
                    headingLvl={3}
                    title={getTranslation(
                        'landing.exclusivePerks.early.text.title',
                    )}
                    msg={perkEarlyTextMsg}
                    renderIcon={this.renderIconMoneySymbolFast}
                />
                <Callout
                    contextCls={`${this.baseCls}__callout-icon`}
                    headingLvl={3}
                    title={getTranslation(
                        'landing.exclusivePerks.noFee.text.title',
                    )}
                    msg={perkNoFeeTextMsg}
                    renderIcon={this.renderIconMoneySymbolZero}
                />
                <Callout
                    contextCls={`${this.baseCls}__callout-icon`}
                    headingLvl={3}
                    title={getTranslation(
                        'landing.exclusivePerks.odProtection.text.title',
                    )}
                    msg={perkDDProtectionTextMsg}
                    renderIcon={this.renderIconShieldMoney}
                />
            </div>
        );
    };

    renderExclusivePerksForSmallScreen = () => {
        const accordionItemId = 'accordionItem-001';
        const AccordionContent = AccordionContentComposer('div');

        return (
            <div
                className={`${this.baseCls}__exclusive-perks-container--small-screen`}
            >
                <ARIAAccordion
                    contextCls={cx(`${this.baseCls}__accordion`)}
                    renderTrigger={this.renderAccordionTrigger}
                    preExpanded={[accordionItemId]}
                >
                    <AccordionContent
                        className={`${this.baseCls}__exclusive-perks-content-panel--small-screen`}
                        accItemProps={{ id: accordionItemId }}
                    >
                        {this.renderExclusivePerksLists()}
                    </AccordionContent>
                </ARIAAccordion>
            </div>
        );
    };

    renderAccordionTrigger = () => {
        return (
            <AccordionItemTrigger
                contextCls={cx(`${this.baseCls}__accordion-item-trigger`)}
                className={`${this.baseCls}__accordionTrigger`}
            >
                {getTranslation('landing.accordionTrigger.header.text')}
            </AccordionItemTrigger>
        );
    };

    renderExclusivePerksLists = () => {
        const { Trans } = reactI18next;
        const perkEarlyTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.early.text.msg"
                components={{ sup: <sup /> }}
            />
        );
        const perkNoFeeTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.noFee.text.msg"
                components={{ sup: <sup /> }}
            />
        );
        const perkDDProtectionTextMsg = (
            <Trans
                i18nKey="landing.exclusivePerks.odProtection.text.msg"
                components={{ sup: <sup /> }}
            />
        );

        const data = [
            {
                id: 'li-1',
                text: getTranslation('landing.exclusivePerks.early.text.title'),
                meta: perkEarlyTextMsg,
                renderIcon: this.renderIconMoneySymbolFast,
            },
            {
                id: 'li-2',
                text: getTranslation('landing.exclusivePerks.noFee.text.title'),
                meta: perkNoFeeTextMsg,
                renderIcon: this.renderIconMoneySymbolZero,
            },
            {
                id: 'li-3',
                text: getTranslation(
                    'landing.exclusivePerks.odProtection.text.title',
                ),
                meta: perkDDProtectionTextMsg,
                renderIcon: this.renderIconShieldMoney,
            },
        ];

        return (
            <List
                shouldAlternateColors
                contextCls={`${this.baseCls}____list-exclusive-perks--small-screen`}
                data={data}
                renderListItem={this.renderAccordionContentLists}
            />
        );
    };

    renderAccordionContentLists = (item) => {
        return (
            <ResourceListItem
                contextCls={`${this.baseCls}__${item.id}`}
                id={item.id}
                meta={item.meta}
                text={item.text}
                renderIcon={item.renderIcon}
            />
        );
    };

    renderInfoBanner = () => {
        const { isAutoSwitchEnabled } = this.props;
        const { Trans } = reactI18next;
        const msg = isAutoSwitchEnabled ? (
            <Trans i18nKey="landing.bannerInfo.msg" />
        ) : (
            <Trans i18nKey="landing.bannerInfo.msg.switchOff" />
        );

        return (
            <Banner
                contextCls={`${this.baseCls}__banner-info`}
                variant="INFO"
                msg={msg}
                renderIcon={this.renderIconCircleInfo}
            />
        );
    };

    renderIconMoneySymbolFast = () => {
        return (
            <EurekaMoneySymbolFast contextCls={`${this.baseCls}__icon-money`} />
        );
    };

    renderIconMoneySymbolZero = () => {
        return (
            <EurekaMoneySymbolZero contextCls={`${this.baseCls}__icon-money`} />
        );
    };

    renderIconShieldMoney = () => {
        return <EurekaShieldMoney contextCls={`${this.baseCls}__icon-money`} />;
    };

    renderIconCircleInfo = () => {
        return <EurekaCircleInfo contextCls={`${this.baseCls}__icon-info`} />;
    };

    renderAccountInfo = () => {
        const data = [
            {
                id: 'li-account',
                text: getTranslation('landing.accountInfo.accountNumber'),
                meta: this.formatAccountInfoNumbers(
                    this.state.directdepositinformation.accountnumber,
                ),
                handleCopyToClipboard: this.handleCopyToClipboard(
                    this.state.directdepositinformation.accountnumber,
                ),
            },
            {
                id: 'li-routing',
                text: getTranslation('landing.accountInfo.routingNumber'),
                meta: this.formatAccountInfoNumbers(
                    this.state.directdepositinformation.routingnumber,
                ),
                handleCopyToClipboard: this.handleCopyToClipboard(
                    this.state.directdepositinformation.routingnumber,
                ),
            },
            {
                id: 'li-bank',
                text: getTranslation('landing.accountInfo.bankName'),
                meta: getTranslation('brand.displayed.name'),
                handleCopyToClipboard: this.handleCopyToClipboard(
                    getTranslation('brand.displayed.name'),
                ),
            },
        ];

        return (
            <List
                shouldAlternateColors
                contextCls={`${this.baseCls}__list-account-info`}
                data={data}
                renderListItem={this.renderAccountInfoListItem}
            />
        );
    };

    renderAccountInfoListItem = (item) => {
        return (
            <ListItem
                id={item.id}
                meta={item.meta}
                text={item.text}
                renderAfter={() => {
                    return this.renderCopyToClipboard(
                        item.handleCopyToClipboard,
                    );
                }}
            />
        );
    };

    renderCopyToClipboard = (handleClick) => {
        return (
            <LinkButton
                contextCls={`${this.baseCls}__button-copy-to-clipboard`}
                text={getTranslation('landing.accountInfo.copyToClipboard')}
                onClick={handleClick}
            />
        );
    };

    renderSetupAutomaticallyBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__button-set-up-automatically`)}
                text={getTranslation('landing.setUpAutomaticallyBtn.text')}
                variant={PRIMARY}
                onClick={this.handleSetupAutomaticallyBtnClick}
                aria-label={getTranslation(
                    'landing.setUpAutomaticallyBtn.text',
                )}
            />
        );
    };

    renderDownloadDDFormBtn = () => {
        const { isAutoSwitchEnabled } = this.props;

        return (
            <Button
                contextCls={cx(`${this.baseCls}__button-download-dd-form`)}
                text={getTranslation('landing.viewDDFormBtn.text')}
                variant={isAutoSwitchEnabled ? SECONDARY : PRIMARY}
                onClick={this.handleDownloadFormBtnClick}
                aria-label={getTranslation('landing.viewDDFormBtn.text')}
            />
        );
    };

    renderActionButtons = () => {
        const { isAutoSwitchEnabled } = this.props;
        const shouldShowSetUpAutomaticallyBtn = isAutoSwitchEnabled;

        return (
            <>
                {this.renderDownloadDDFormBtn()}
                {shouldShowSetUpAutomaticallyBtn &&
                    this.renderSetupAutomaticallyBtn()}
            </>
        );
    };

    renderTips = () => {
        const { Trans } = reactI18next;

        return (
            <div className={`${this.baseCls}__tips`}>
                <p className={`${this.baseCls}__tips-content`}>
                    <Trans
                        i18nKey="landing.tips.content"
                        values={{
                            brandDisplayedName: getTranslation(
                                'brand.displayed.name',
                            ),
                        }}
                    />
                </p>
                <p>
                    {getTranslation('landing.tips.needHelp')}
                    <Hyperlink href={this.state.faqLink} shouldOpenInNewWindow>
                        {getTranslation('landing.tips.faq')}
                    </Hyperlink>
                </p>
            </div>
        );
    };

    handleCopyToClipboard = (content) => {
        return () => {
            navigator.clipboard.writeText(content);

            Toaster({
                message: getTranslation('landing.copyToClipboard'),
                variant: TOASTER_VARIANTS.SUCCESS,
                position: 'bottom-center',
                shouldHideProgressBar: true,
            });
        };
    };

    handleSetupAutomaticallyBtnClick = () => {
        const path = ROUTES_CONSTS.BUMP;
        const { navigateWithinParent } = this.props;
        navigateWithinParent(path);
    };

    handleDownloadFormBtnClick = async () => {
        const { onSuspensionStart, onSuspensionFinish } = this.props;

        try {
            onSuspensionStart();
            const { output } = await getDDManualForm();
            this.downloadPDF(output);
        } catch (e) {
            raisePageError();
        } finally {
            onSuspensionFinish();
        }
    };

    fetchAccountData = async () => {
        let accountData = {};

        try {
            accountData = await getAccountData();
        } catch (error) {
            raisePageError();
        } finally {
            this.setState({ shouldShowLandingPage: true, ...accountData });
        }
    };

    fetchFaqLink = async () => {
        const faqLink = { faqLink: '/manage/help' };

        try {
            const helpCenterInfo = await getHelpCenterInfo();
            const { categoryLinks } = helpCenterInfo;

            const linkObj = categoryLinks['Direct Deposit'];
            faqLink.faqLink = linkObj.url;
        } catch (error) {
            // No need to handle error here, if error, use the default url defined
        } finally {
            this.setState(faqLink);
        }
    };

    downloadPDF = (pdf) => {
        const byteArray = new Uint8Array(
            window
                .atob(pdf)
                .split('')
                .map((char) => char.charCodeAt(0)),
        );
        const pdfFile = new Blob([byteArray], {
            type: 'application/pdf',
        });
        saveAs(pdfFile, 'DD_Manual_Form.pdf');
    };

    formatAccountInfoNumbers = (number) => {
        return number ? number.match(/.{1,3}/g).join(' ') : '';
    };

    baseCls = 'dd-switch-landing';
}

export default withRouter(
    SuspensionComposer(Landing, () => ({
        title: getTranslation('statusModal.downloading'),
    })),
);
