import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { IllustrationCallout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import { getImagePathForKey } from '@Green-Dot-Corporation/eureka-ui-media';
import {
    getTranslation,
    reactI18next,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import { VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { withRouter } from '@Green-Dot-Corporation/eureka-lib-router-utils';
import { Hyperlink } from '@Green-Dot-Corporation/eureka-ui-typography';
import { IMAGE_KEYS } from '../../media/images/imageLibrary';
import { ROUTES_CONSTS } from '../../const/routesConst';
import DDSwitchContainer from '../dd-switch-container/DDSwitchContainer';
// Services
import { getHelpCenterInfo } from '../../services/DDSwitchService';

const { PRIMARY } = VARIANTS;
const { Trans } = reactI18next;

class BumpAttemptDeclined extends PureComponent {
    static propTypes = {
        navigateWithinParent: PropTypes.func,
    };

    state = {
        faqLink: '',
    };

    render() {
        const calloutProps = {
            title: getTranslation('ddswitch.bump.attempt.error.heading'),
            msg: this.renderMessage(),
            contextCls: `${this.baseCls}__illustration-callout`,
            illustrationSrc: getImagePathForKey(
                IMAGE_KEYS.WARNING_ILLUSTRATION,
            ),
            illustrationAltText: getTranslation(
                'ddswitch.bump.attempt.error.imgAlt',
            ),
            headingLvl: 5,
            actions: [
                {
                    variant: PRIMARY,
                    text: getTranslation('ddswitch.bump.attempt.error.button'),
                },
            ],
            onActionClick: this.handleActionClick,
        };

        return (
            <DDSwitchContainer contextCls={`${this.baseCls}__container`}>
                <IllustrationCallout {...calloutProps} />
            </DDSwitchContainer>
        );
    }

    componentDidMount() {
        this.fetchFaqLink();
    }

    renderMessage = () => {
        const linkCls = `${this.baseCls}__link`;

        return (
            <div className={`${this.baseCls}__message`}>
                <Trans
                    i18nKey="ddswitch.bump.attempt.error.message"
                    components={{
                        faqLink: (
                            <Hyperlink
                                href={this.state.faqLink}
                                contextCls={linkCls}
                                shouldOpenInNewWindow
                            />
                        ),
                    }}
                />
            </div>
        );
    };

    handleActionClick = () => {
        const path = ROUTES_CONSTS.LANDING;
        const { navigateWithinParent } = this.props;
        navigateWithinParent(path);
    };

    fetchFaqLink = async () => {
        const faqLink = { faqLink: '/manage/help' };

        try {
            const helpCenterInfo = await getHelpCenterInfo();
            const { categoryLinks } = helpCenterInfo;

            const linkObj = categoryLinks['Direct Deposit'];
            faqLink.faqLink = linkObj.url;
        } catch (error) {
            // No need to handle error here, if error, use the default url defined
        } finally {
            this.setState(faqLink);
        }
    };

    baseCls = 'dd-switch-bump-attempt-declined';
}

export default withRouter(BumpAttemptDeclined);
