import { get, patch, post } from '@Green-Dot-Corporation/web-app-utils-api-lib';
import { getUrlConfig } from '../utils/urlHelper';

export default {};

export const getDDFeatureConfig = async () => {
    let isAutoSwitchEnabled = false;

    try {
        const { GET_FEATURE_LIBRARY } = getUrlConfig();
        const configResp = await get(GET_FEATURE_LIBRARY);

        const depositAutoSwitchFeature = configResp.library.features.filter(
            (i) => i.featurename === 'DepositAutoSwitch',
        )[0];

        isAutoSwitchEnabled =
            depositAutoSwitchFeature.featurestatus === 'enabled';
    } catch (e) {
        // failover gracefully
    }

    const resp = {
        isAutoSwitchEnabled,
    };

    return resp;
};

let accountData;
export const getAccountData = async () => {
    if (!accountData) {
        const { GET_ACCOUNT_DETAIL } = getUrlConfig();

        const { directdepositinformation, accountidentifier } = await get(
            GET_ACCOUNT_DETAIL,
        );

        accountData = {
            directdepositinformation,
            accountidentifier,
        };
    }

    return accountData;
};

export const clearAccountData = () => {
    accountData = undefined;
};

let helpCenterInfo;
export const getHelpCenterInfo = async () => {
    const { GET_HELP_CENTER_INFO } = getUrlConfig();

    if (!helpCenterInfo) {
        helpCenterInfo = await get(GET_HELP_CENTER_INFO);
    }

    return helpCenterInfo;
};

export const getTerms = async (accountIdentifier) => {
    const { GET_TERMS } = getUrlConfig();
    const getTermsUrl = GET_TERMS.replace(
        '{accountIdentifier}',
        accountIdentifier,
    );

    const terms = await get(getTermsUrl);

    return terms;
};

export const requestDDSwitchVendorToken = async () => {
    const { REQUEST_DD_SWITCH_VENDOR_TOKEN } = getUrlConfig();
    const rawResp = await post(REQUEST_DD_SWITCH_VENDOR_TOKEN);
    const resp = {
        switchId: rawResp.switchId,
        vendorName: rawResp.vendorName,
        vendorToken: rawResp.vendorToken,
    };

    return resp;
};

export const getDDManualForm = async () => {
    const { GET_DD_MANUAL_FORM } = getUrlConfig();
    const getDDManualFormResp = await get(GET_DD_MANUAL_FORM);

    return getDDManualFormResp;
};

export const updateSwitchAttempt = async (switchId, status) => {
    const request = {
        status,
    };

    const requestOptions = {
        body: JSON.stringify(request),
    };

    const { UPDATE_SWITCH_ATTEMPT } = getUrlConfig();
    const updateSwitchAttemptUrl = UPDATE_SWITCH_ATTEMPT.replace(
        '{switchId}',
        switchId,
    );

    const updateSwitchAttemptResp = await patch(
        updateSwitchAttemptUrl,
        requestOptions,
    );

    return updateSwitchAttemptResp;
};
